import IPaymentGateway = require('../interfaces/IPaymentGateway');
import Soap = require('soap');

class CybersourceGateway implements IPaymentGateway {
	cybersourceUrl: string;
	merchantID: string;
	transactionKey: string;

	public constructor() {
		this.cybersourceUrl = "https://ics2wstest.ic3.com/commerce/1.x/transactionProcessor/CyberSourceTransaction_1.93.wsdl";
		this.merchantID = "rangle_dev";
		this.transactionKey = "";
	}

	private getSecurity() {
		return new Soap.WSSecurity(this.merchantID, this.transactionKey);
	}

	authorize(cc: any, callback) {
		console.log("CC", cc);
		var merchant = this.merchantID;
		var ws = this.getSecurity();

		Soap.createClient(this.cybersourceUrl, function (err, client) {
			client.setSecurity(ws);

			var data = {
				merchantID: merchant,
				merchantReferenceCode: "TEST",
				clientLibrary: 'node.js',
				purchaseTotals: {
					currency: "USD",
					grandTotalAmount: 11.35
				},
				ccCaptureService: {
					attributes: {
						run: true,
					},
					authRequestID: "4756900151956784004012"
				}
			}

			client.runTransaction(data, function (err, result) {
				if (err) {
					console.log(err);

					callback(err, null);
				}

				callback(null, result);
			});
		});
	}

	capture(cc: any, callback) {
		var merchant = this.merchantID;
		var ws = this.getSecurity();

		Soap.createClient(this.cybersourceUrl, function (err, client) {
			client.setSecurity(ws);

			var data = {
				merchantID: merchant,
				merchantReferenceCode: "TEST",
				clientLibrary: 'node.js',
				billTo: {
					firstName: "Sonu",
					lastName: "Kapoor",
					street1: "129 Spadina Av",
					city: "Toronto",
					state: "ON",
					postalCode: "M5V 2L3",
					country: "CA",
					email: "sonu.kapoor@rangle.io"
				},
				item: {
					attributes: {
						id: 0
					},
					unitPrice: 10.35,
					quantity: 2,
					productCode: '2001',
					productName: 'Blank T-Shirt',
					productSKU: "20010102"
				},
				purchaseTotals: {
					currency: "USD",
					grandTotalAmount: 10
				},
				card: {
					accountNumber: "4111111111111111",
					expirationMonth: "12",
					expirationYear: "2016",
					cvNumber: "122",
					cardType: "001"
				},
				ccAuthService: {
					attributes: {
						run: true,
					}
				}
			};
			client.runTransaction(data, function (err, result) {
				if (err)
					callback(err, null);

				callback(null, result);
			});
		});
	}
}

export = CybersourceGateway;


